@import url(https://fonts.googleapis.com/css?family=Manjari:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.table-flex{display:flex}

body{font-family:Manjari, sans-serif;font-size:14px}.calendar{position:absolute;width:350px;margin:100px auto 0;box-shadow:1px 2px 3px 4px rgba(0,0,0,0.1);border-radius:6px}.header-calendar{display:flex;align-items:center;padding:12px;border-radius:6px 6px 0 0;background-color:#1abc9c;color:#fff}.nav--prev{width:30px;height:30px;border:1px solid #fff;border-radius:99px;outline:0;background-color:transparent;color:#fff;cursor:pointer}.datetime{margin:0 auto;font-size:18px;font-weight:bold}.week-container,.day-container{display:flex;flex-wrap:wrap}.week-cell,.day-cell{flex-grow:0;flex-shrink:0;flex-basis:calc(100% / 7);display:flex;justify-content:center;align-items:center;height:30px}.day-cell--faded{opacity:.4}.day-cell--today{background-color:#1abc9c;color:#fff}

.table{border:1px solid black;display:flex;flex-direction:column;height:200px;width:20%}.table-header{position:relative;display:flex;flex-direction:column;border:1px solid black}.table-item-input{position:absolute;left:200px}.draggable{border:1px solid blue}.table-content{background:whitesmoke;border:2px solid black;height:199px}.draggable{margin:10px;display:flex;flex-direction:column}

